let mvc = {
	activeClass: 0,
	page: 1,
	loadingType: 'more', //加载更多状态
	type: 'add',
	thispage: 0,
	Allpage: 0,
	seckList: [],
	navHeight: 0,
	navList: [{
			title: '13:00',
			status: true
		},
		{
			title: '14:00',
			status: false
		},
		{
			title: '15:00',
			status: false
		}, {
			title: '16:00',
			status: false
		}, {
			title: '17:00',
			status: false
		}
	]
}
import api from '@/common/index.js';
import uniCountdown from '@/components/uni-countdown/uni-countdown.vue';
import uniNumberBox from '@/components/uni-number-box.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import imglazy from '@/components/img-lazy/img-lazy.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
export default {
	components: {
		uniCountdown,
		uniNumberBox,
		uniLoadMore,
		imglazy,
		uniNavBar
	},
	data() {
		return {
			imglazyStyle: 'border-radius: 10upx;',
			mvc: {
				navHeight: 0,
				activeClass: 0,
				page: 1,
				loadingType: 'more', //加载更多状态
				type: 'add',
				thispage: 0,
				Allpage: 0,
				seckList: [],
				navList: [{
						title: '13:00',
						status: true
					},
					{
						title: '14:00',
						status: false
					},
					{
						title: '15:00',
						status: false
					}, {
						title: '16:00',
						status: false
					}, {
						title: '17:00',
						status: false
					}
				]
			}
		}
	},
	computed: {
		i18n() {
			return this.$t('list')
		}
	},
	onLoad() {
		let that = this;
		setTimeout(function(item, index) {
			that.getTitleHeight()
			that.loadData()
		}, 300)
	},
	methods: {
		// 高度
		getTitleHeight() {
			let that = this;
			uni.getSystemInfo({
				success: res => {
					console.log(res.screenHeight)
					if (res.screenHeight < 750) {

						mvc.navHeight = "125px"
						return false;
					}
					if (res.screenHeight < 950) {
						mvc.navHeight = "135px"

						return false;
					}
					if (res.screenHeight > 1000) {
						mvc.navHeight = "195px";

						return false;
					}
				}
			})
		},
		// 获取数据
		loadData() {
			let that = this;
			let params = {
				type: "APP"
			};
			let list_data = {
				page: mvc.page,
				downNum: ""
			};
			that.$ajax.hNew(list_data, params).then(res => {
				that.allPage(res);
			})
		},
		allPage(res) {
			let that = this;
			//当商品数据为空时
			if (res.list == null || res.other.totalPage == 0) {
				mvc.loadingType = "nomore";
				uni.hideLoading();
				that.mvc = mvc;
				// that.cartoon = true;
				return false;
			};
			// mvc.prciceHdid = true;
			mvc.Allpage = res.other.totalPage;
			mvc.thispage = res.other.thispage;
			let banner = res.list;
			for (var i = 0; i < banner.length; i++) {
				let banner_img = that.$store.getters.imgShow + '/thumb/' + banner[i].item_no + '.jpg?x-oss-process=style/300';
				banner[i].image = banner_img;
				banner[i].allNum = parseInt(banner[i].spec);
				banner[i].min = parseInt(banner[i].spec);
				banner[i].circleHidden = false;
				if (banner[i].cnum != "0") {
					// banner[i].circleHidden = true;
				}
			}
			let seckList = banner;
			mvc.loadingType = res.other.thispage > res.other.totalPage ? 'nomore' : 'more';
			//没有更多直接返回
			//请求分页
			if (mvc.type == 'add') {
				mvc.page = mvc.page + 1;

				if (mvc.loadingType === 'nomore') {
					that.mvc = mvc;
					return;
				}
				mvc.loadingType = 'loading';
			} else {
				mvc.loadingType = 'more'
			}
			// 刷新页面
			if (mvc.type == 'refresh') {
				mvc.seckList = [];
				uni.stopPullDownRefresh();
				mvc.page = mvc.page + 1;
				if (mvc.loadingType === 'nomore') {
					that.mvc = mvc;
					return;
				}
				mvc.loadingType = 'loading';
			}
			// 当前页面小于20条
			if (seckList.length < 20) {
				mvc.loadingType = 'nomore';
				uni.hideLoading();
				mvc.seckList = mvc.seckList.concat(seckList);
				that.mvc = mvc;
				return false;
			}
			uni.hideLoading();
			mvc.seckList = mvc.seckList.concat(seckList);
			that.mvc = mvc;
		},
		// 切换导航
		changenav(item, index) {
			mvc.activeClass = index;
			this.mvc = mvc;
		},
		// 倒计时完成
		timeup(e) {
			console.log(e)
			uni.showToast({
				title: '时间到'
			})
		},
		//下拉刷新
		onPullDownRefresh() {
			mvc.page = 1;
			mvc.type = 'refresh';
			this.loadData();
		},
		//加载更多
		onReachBottom() {
			mvc.type = 'add';
			this.loadData();
		},
	},

}
